<template>
  <div class="progress">
    <div class="progress-bar" :style="width"></div>
  </div>
</template>

<script>
export default {
  props: {
    max: Number,
    val: Number,
  },
  computed: {
    width() {
      let w = (this.val / this.max) * 100;
      if (w > 100) {
        w = 100;
      } else if (w < 0) {
        w = 0;
      }

      let width = {
        width: w + "%",
      };

      let goldWidth = {
        width: w + "%",
        background: "linear-gradient(120deg, #d4fc79 0%, #96e6a1 100%)",
      };

      let purpleWidth = {
        width: w + "%",
        background: "linear-gradient(120deg, #f6d365 0%, #fda085 100%)",
      };


      let sumWidth = {};

      if (w >= 50) {
        sumWidth = purpleWidth;
      } else if(w >= 20){
        sumWidth = goldWidth;
      } else {
        sumWidth = width;
      }
      return sumWidth;
    },
  },
};
</script>
