<template>
  <div class="progress">
    <div class="progress-bar" :style="width"></div>
  </div>
</template>

<script>
export default {
  props: {
    max: Number,
    val: Number,
  },
  computed: {
    width() {
      let w = (this.val / this.max) * 100;
      if (w > 100) {
        w = 100;
      } else if (w < 0) {
        w = 0;
      }
      let resultWidth = {
        width: w + "%",
      };
      return resultWidth;
    },
  },
};
</script>

<style>
.progress-bar {
  background-image: linear-gradient(to right, #4facfe 0%, #00f2fe 100%);
}
</style>
