<template>
  <div class="container">
    <h1 class="bg-primary h1">TEST YOUR LUCK</h1>
    <div class="underHeader">
      <button class="btn btn-primary" @click="clearProfit()" :disabled="clear">
        {{ btnText }}
      </button>
      <h3 class="maxLuck">Max profit - {{ maxProfit() }}</h3>
      <h3 class="maxLuck">Max luck - {{ maxLuck() }} %</h3>
    </div>
    <hr />
    <h3 v-show="showRes" class="alert alert success">
      Your luck: {{ mathLuck }} %
    </h3>
    <hr />
    <app-progress-gold :val="sum" :max="maxNumbers * 5"> </app-progress-gold>
    <hr />
    <transition name="slide">
      <h3 v-show="showRes" class="alert alert success">
        Your profit: {{ sum }}
      </h3>
    </transition>
    <hr />
    <app-Progress :val="numbers.length" :max="maxNumbers"> </app-Progress>

    <hr />
    <button class="btn btn-success" @click="addNumber" :disabled="done">
      Add number
    </button>
    <button class="btn btn-success" @click="playAgain" :disabled="tryAgain">
      Try again
    </button>
    <hr />
    <ul class="list-group">
      <li class="list-group-item" v-for="number in numbers" :key="number.id">
        {{ number }}
      </li>
    </ul>
  </div>
</template>

<script>
import Progress from "./components/progress-bar.vue";
import ProgressGold from "./components/progress-bar-lucky.vue";

export default {
  data() {
    return {
      showRes: true,
      numbers: [],
      maxNumbers: 10,
      profit: 0,
      luck: 0,
    };
  },
  methods: {
    addNumber() {
      if (!this.done) {
        let rnd = Math.floor(Math.random() * 11) - 5;
        this.numbers.push(rnd);
      }
    },
    playAgain() {
      this.numbers = [];
    },

    maxProfit() {
      if (this.sum > this.profit) {
        this.profit = this.sum;
      } 
     return this.profit;
    },
    maxLuck() {
      if (this.mathLuck > this.luck) {
        this.luck = this.mathLuck;
      } 
      console.log(typeof this.mathLuck);
      console.log(typeof this.luck);
      return this.luck;
    },
    clearProfit() {
      this.profit = 0;
      this.luck = 0;
      console.log(this.profit)
      console.log(this.luck)
    },
  },
  computed: {
    sum() {
      let sum = 0;
      for (let i = 0; i < this.numbers.length; i++) {
        sum += this.numbers[i];
      }
      return sum;
    },
    mathLuck() {
      let sumLuck = 0;
      sumLuck = (this.sum / (this.maxNumbers * 5)) * 100;
      let roundedSumLuck = Number(sumLuck.toFixed(2));
      if (roundedSumLuck <= 0) {
        roundedSumLuck = 0;
      }
      return roundedSumLuck;
    },
    btnText() {
      // return this.showH2 ? "Hide result" : "Show result";
      return "Clear";
    },
    done() {
      return this.numbers.length >= this.maxNumbers;
    },
    tryAgain() {
      return this.numbers.length < this.maxNumbers;
    },
    clear() {
      return this.numbers.length !== 0;
    }
  },
  components: {
    appProgress: Progress,
    appProgressGold: ProgressGold,
  },
};
</script>

