  padding: 10px;
  color: #fff;
  background-image: linear-gradient(to right, #4facfe 0%, #00f2fe 100%);
}
.btn-success {
  color: #555;
  margin-right: 20px;
  background-image: linear-gradient(120deg, #d4fc79 0%, #96e6a1 100%);
  border: none;
}
.slide-enter {
  opacity: 0;
}
.slide-enter-active {
  transition: opacity 0.5s;
}
.slide-leave-active {
  transition: opacity 0.5s;
}
.slide-leave-to {
  opacity: 0;
}
.maxLuck {
  display: inline-block;
  margin: 20px;
}
.underHeader {
  display: flex;
  align-items: center;
}
